/* eslint-disable no-unused-vars */
import {
  appendHeader,
  assignAdminNavbar,
  correctNavbarHref,
  fetchUrl,
  formatCurrency,
  getToken,
  logoutBtnOnClick,
  redirectTo,
} from "../../util";
import { ROLES, monthsArr } from "../../util/constants";
import {
  XANO_ADMIN_FINANCE_URL,
  XANO_ADMIN_GET_MEMBERSHIP_REVENUE_URL,
  XANO_ADMIN_SIGN_IN_REDIRECT,
} from "../../util/xano_urls";

(() => {
  let UrlGymId, selectedFinanceMonth;

  window.onload = async function () {
    logoutBtnOnClick("/admin/sign-in", "sidebar-logout-btn");

    var curUrl = new URL(document.location.href);
    UrlGymId = curUrl.searchParams.get("gymCode") || null;

    if (UrlGymId) correctNavbarHref(UrlGymId);
    else {
      // ? TODO: Change this to a modal showcasing the error
      console.error("No gym id found");
      return;
    }

    const token = getToken();
    if (token) {
      let result = await fetchUrl(XANO_ADMIN_FINANCE_URL + UrlGymId, "GET", false, token);

      if (result.isFetchOk !== undefined && !result.isFetchOk) {
        console.error(result.message);
        // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
        return;
      }

      const { team_role, ...data } = result;

      if (team_role === ROLES.SUPER_ADMIN || team_role === ROLES.ADMIN) {
        assignAdminNavbar(team_role);
        getSuperAdminData(data);
      } else {
        redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=UNAUTHORIZED_USER`);
      }
    } else {
      redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
    }
  };

  async function getSuperAdminData(data) {
    const { gym, finances } = data;

    if (gym) appendHeader(gym);
    
    let indx;
    let isMonthFound = finances.some((month, i) => {
      if (month.month === new Date().getMonth() + 1 && month.year === new Date().getFullYear()) {
        indx = i;
        return true;
      } else false;
    });

    selectedFinanceMonth = isMonthFound ? finances[indx] : finances[finances.length - 1];
    if (selectedFinanceMonth === undefined) selectedFinanceMonth = null;
    appendStatistics(selectedFinanceMonth);
    appendAllFinance(finances);
    appendChartData(finances);

    // const addNewEntryContainer = document.getElementById("add-new-entry-container");
    // if (team_role === ROLES.ADMIN) {
    //   if (addNewEntryContainer) addNewEntryContainer.remove();
    // } else if (team_role === ROLES.SUPER_ADMIN) {
    //   if (addNewEntryContainer) {
    //     addNewEntryContainer.style.display = "block";
    //     initMembershipRevenue(token);
    //   }
    // }
  }

  function appendStatistics(finances) {
    if (!finances) {
      document.getElementById("gym-statistics-month-year").innerText = "This months data not found";
      document.getElementById("financial-expenses").innerText = "No data found";
      document.getElementById("financial-revenue").innerText = "No data found";
      document.getElementById("financial-profit").innerText = "No data found";
    } else {
      document.getElementById("gym-statistics-month-year").innerText =
        monthsArr[finances.month - 1] + " " + finances.year;

      document.getElementById("financial-expenses").innerText = formatCurrency(Number(finances.additional_expenses));
      document.getElementById("financial-revenue").innerText = formatCurrency(
        Number(finances.gross_revenue) + Number(finances.additional_revenue)
      );
      document.getElementById("financial-profit").innerText = formatCurrency(finances.net_profit);
    }
  }

  function appendAllFinance(data) {
    const financeContainer = document.getElementById("financial-data-container");
    const og_webflow_element = financeContainer.querySelector("#financial-data-item");

    data.forEach((monthData) => {
      const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
      clonedCard.setAttribute("id", "");
      clonedCard.setAttribute(
        "href",
        "/admin/gym/financial-month?gymCode=" + UrlGymId + "&month=" + monthData.month + "&year=" + monthData.year
      );

      clonedCard.querySelector("#financial-data-month-year ").innerText =
        monthsArr[monthData.month - 1] + " " + monthData.year;

      clonedCard.querySelector("#financial-data-revenue").innerText = formatCurrency(
        Number(monthData.gross_revenue) + Number(monthData.additional_revenue)
      );

      clonedCard.querySelector("#financial-data-expenses").innerText = formatCurrency(
        Number(monthData.additional_expenses)
      );
      clonedCard.querySelector("#financial-data-profit").innerText = formatCurrency(monthData.net_profit);

      if (monthData.net_profit < 0) {
        clonedCard.querySelector("#financial-indicator").classList.add("is-loss");
        clonedCard.querySelector("#financial-data-profit").style.color = "#ff634e";
      } else clonedCard.querySelector("#financial-indicator").classList.add("is-profitable");

      financeContainer.prepend(clonedCard);
    });

    og_webflow_element.remove();
  }

  function appendChartData(data) {
    const ctx = document.getElementById("finance-chart").getContext("2d");

    //   ctx.canvas.parentNode.style.height = "400px";
    //   ctx.canvas.parentNode.style.width = "400px";

    let labels = [...data.map((month) => monthsArr[month.month - 1])];

    // eslint-disable-next-line no-undef
    new Chart(ctx, {
      type: "bar",
      data: {
        labels,
        datasets: [
          {
            label: "Revenue",
            data: [...data.map((month) => Number(month.gross_revenue) + Number(month.additional_revenue))],
            backgroundColor: ["#00EDF4"],
          },
          {
            label: "Profit",
            data: [...data.map((month) => month.net_profit)],
            backgroundColor: ["#E0FFC1"],
          },
          {
            label: "Expenses",
            data: [...data.map((month) => -Number(month.additional_expenses))],
            backgroundColor: ["#ff0000"],
          },
        ],
      },
      options: {
        scales: {
          x: {
            grid: { display: false },
            stacked: true,
          },
          y: {
            grid: { display: false },
            stacked: true,
          },
        },
      },
    });
  }

  // function initMembershipRevenue(token) {
  //   const monthSelect = document.getElementById("month-select");
  //   const yearSelect = document.getElementById("year-select");
  //   const membershipRevenueEl = document.getElementById("membership-revenue-input");
  //   const revenueEl = document.getElementById("revenue-input").value || 0;
  //   const expensesEl = document.getElementById("expenses-input").value || 0;
  //   membershipRevenueEl.disabled = true;

  //   async function fetchMembershipRevenueFn() {
  //     const xano_input = {
  //       time: new Date(`${28 + monthSelect.value + yearSelect.value}`).getTime().toString(),
  //     };
  //     let data = await fetchUrl(XANO_ADMIN_GET_MEMBERSHIP_REVENUE_URL + UrlGymId, "POST", xano_input, token);

  //     if (data.isFetchOk !== undefined && !data.isFetchOk) {
  //       console.error(data.message);
  //       // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
  //       return;
  //     }
  //     membershipRevenueEl.value = data;

  //     document.getElementById("profit-input").value = parseInt(data) + parseInt(revenueEl) - parseInt(expensesEl);
  //   }
  //   // ? when the user clicks on the select dropdown, it will get data
  //   fetchMembershipRevenueFn();
  //   monthSelect.onchange = fetchMembershipRevenueFn;
  //   yearSelect.onchange = fetchMembershipRevenueFn;
  // }
})();
